<template>
    <div class="p-2">
        <el-row>
            <el-col :md="9" :sm="24" :offset="1">版块</el-col>
            <el-col :md="13" :offset="1" class="hidden-sm-and-down">最新</el-col>
        </el-row>
        <div v-for="(item, index) in categories" :key="index"  id="categoty">
            <!-- index % colors.length: 表示用索引号 除 颜色数量取余数,实现在多个颜色的循环使用 -->
            <latest-topic :item="item" :color="colors[index % colors.length]"></latest-topic>
        </div>
    </div>
</template>

<script>
import { Loading } from "element-ui";   // 导入 Loading加载
import CategoryService from "../../../api/service/category_service";
import Category from '../../../model/category';

export default {
    name: 'BoardList',
    components: {
        // Board: () => import('../components/Board.vue')
        LatestTopic: () => import('../components/LatestTopic.vue'),

    },
    data: () => ({
        colors: ['red', 'blue', 'Gold', 'SeaGreen', 'DarkMagenta'],
        categories: [],
        pageSize: 10,        // 每页大小
        currentPage: 1,      // 当前页
        loading: '',            // 用于显示 Loading 加载 的变量
    }),
    mounted() {
        this.getCategories();
    },
    methods: {
        async getCategories() {
            this.showLoading('#categoty', '正在获取数据...');  // 显示 Loadding加载
            this.categories = [];
            let skip = (this.currentPage - 1) * this.pageSize;
            let response = await CategoryService.fetchAll(this.pageSize, skip, [], ['-level', '-createdAt']);
            if (response.status_code === "ok") {
                this.categories = response.reslut.map(item => { return new Category().fromJson(item) });
            }else{
                this.$notify.error({
                            title: '获取数据失败',      // 标题
                            message: '无法从服务器获取有效数据，请检查网络连接情况',     // 消息内容
                            // offset: 100,        // 偏移量，消息距屏幕边缘偏移一段距离
                            showClose: false,      // 不显示关闭按钮，只能等自动关闭
                        });
            }

            this.endLoading();  // 停止显示 Loading加载

        },



         /**
        * 自定义用于局部区域显示 loading 的函数
        * @param {*} targetNode  目标区域的选择器，建设使用 id 选择器
        * @returns 
        */
        showLoading(targetNode, message) {
            this.loading = Loading.service({
                // 锁定屏幕的滚动
                lock: true,
                // 显示的文本
                text: message,
                // document.querySelector 用于以获取到对应 DOM 节点
                target: document.querySelector(targetNode),
            });
        },
        
        // 停止显示 loading
        endLoading() {
            this.loading.close();
        }
    },


}
</script>

